import * as SplashScreen from 'expo-splash-screen';
import React, { useRef } from 'react';
import { Animated, Easing, View } from 'react-native';
import { useTheme } from 'react-native-paper';
import Image from '../../atoms/Image';
import makeStyles from './css';

interface IProps {
}

const AppLoading = (props: IProps) => {
    SplashScreen.hideAsync();
    const theme = useTheme();
    const styles = makeStyles(theme);

    const logoOpacityAnimator = useRef(new Animated.Value(0)).current;
    React.useEffect(() => {
        Animated.loop(Animated.timing(logoOpacityAnimator, {
            useNativeDriver: true,
            duration: 2000,
            toValue: 200,
            isInteraction: false,
            easing: Easing.linear
        })).start();
    });

    const logoOpacity = logoOpacityAnimator.interpolate({
        inputRange: [0, 45, 55, 100, 145, 155, 200],
        outputRange: [0, .3, .5, 1, .5, .3, 0],
    });

    return (
        <View style={styles.root}>
            <View style={styles.overlay}></View>
            <Animated.View style={[styles.logoContainer, { opacity: logoOpacity }]}>
                <Image resizeMode="contain"
                    style={styles.logo}
                    source={require('@assets/images/logo.png')} />
            </Animated.View>
        </View>
    );
};
export default AppLoading;
