import { IconButton } from '@atoms';
import { CustomTheme } from '@theme';
import React from 'react';
import { StyleProp, View, ViewStyle } from 'react-native';
import { useTheme } from 'react-native-paper';
import { useNavigate } from 'react-router';
import makeStyles from './css';

interface IProps {
    style?: StyleProp<ViewStyle> | undefined;
    disabled?: boolean;
}

const PrevPageButton = (props: IProps) => {
    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const navigate = useNavigate();
    const backAction = (): boolean | null | undefined => {
        if (!props.disabled) {
            navigate(-1);
        }
        return false;
    }

    return (
        <View style={[styles.root, props.style]}>
            <IconButton iconStyle={styles.icon}
                icon={require('@assets/images/arrow-right-white-icon.png')}
                rippleColor="transparent"
                buttonProps={{
                    onPress: backAction,
                }} />
        </View>
    );
};
export default PrevPageButton;
