import { Text, TextInput } from '@atoms';
import { CustomTheme } from '@theme';
import { FormikErrors, FormikTouched } from 'formik';
import React, { useEffect, useRef, useState } from 'react';
import { Animated, Easing, LayoutChangeEvent, TextInputProps as RNTextInputProps, View } from 'react-native';
import { useTheme } from 'react-native-paper';
import { TextInputProps } from 'react-native-paper/lib/typescript/components/TextInput/TextInput';
import makeStyles from './css';

interface IProps {
    type?: 'string' | 'number';
    inputProps?: RNTextInputProps;
    inputKey: string;
    value: string;
    label?: string;
    placeholder?: string;
    touched: FormikTouched<any>;
    errors: FormikErrors<any>;
    withErrorText?: boolean;
    formSubmited?: boolean;
    setFieldValue: (field: string, value: any, shouldValidate?: boolean | undefined) => Promise<FormikErrors<any>> | Promise<void>;
    setFieldTouched?: (field: string, touched?: boolean, shouldValidate?: boolean | undefined) => Promise<FormikErrors<any>> | Promise<void>;
    setFieldError?: (field: string, value: string | undefined) => void;
}

const TextInputForm = (props: IProps) => {
    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const type = props.type ?? 'string';
    const withErrorText = props.withErrorText ?? true;

    const additionalInputProps: TextInputProps = {
        theme,
        mode: 'outlined',
        autoCompleteType: 'off',
        autoCapitalize: 'none',
        autoCorrect: false,
    };
    const inputProps: TextInputProps = Object.assign({}, additionalInputProps, props.inputProps);

    const onChangeText = (text: string) => {
        let valid = true;
        if (type === 'number') {
            valid = /^\d*$/.test(text);
        }
        if (valid) {
            if (props.setFieldTouched) {
                props.setFieldTouched(props.inputKey, true);
            }
            props.setFieldValue(props.inputKey, text);
        }
    }

    const [focused, setFocused] = useState(false);
    const onFocus = () => {
        setFocused(true);
    }

    const onBlur = () => {
        setFocused(false);
        if (props.setFieldTouched) {
            props.setFieldTouched(props.inputKey, true);
        }
    }

    const getFieldTouched = (fieldName: string) => {
        return props.formSubmited || props.touched[fieldName];
    }

    const placeholder = !props.label ? props.placeholder : '';

    const labelAnimator = useRef(new Animated.Value(0)).current;
    useEffect(() => {
        labelAnimator.stopAnimation();
        const toValue = focused || props.value ? 1 : 0;
        Animated.timing(labelAnimator, {
            useNativeDriver: true,
            duration: !props.value ? 200 : 0,
            toValue,
            isInteraction: false,
            easing: Easing.ease
        }).start();
    }, [focused, props.value]);

    const [labelWidth, setLabelWidth] = useState(0);
    const onLabelLayout = (event: LayoutChangeEvent) => {
        if (!labelWidth) {
            const { width } = event.nativeEvent.layout;
            setLabelWidth(width);
            // console.log(width);
        }
    }

    const labelMove = {
        transform: [
            {
                scale: labelAnimator.interpolate({
                    inputRange: [0, 1],
                    outputRange: [1, .75]
                })
            },
            {
                translateX: labelAnimator.interpolate({
                    inputRange: [0, 1],
                    outputRange: [-8, -8 + labelWidth * .25 / 2]
                })
            },
            {
                translateY: labelAnimator.interpolate({
                    inputRange: [0, 1],
                    outputRange: [18, -8]
                })
            },
        ]
    };

    const inputRef = useRef<any>(null);
    const onLabelClick = () => {
        inputRef.current?.forceFocus();
    }

    return (
        <View style={styles.root}>
            {
                !!props.label &&
                <Animated.Text onLayout={onLabelLayout} onPress={onLabelClick} style={[
                    styles.label,
                    focused ? styles.labelFocused : null,
                    labelMove
                ]}>
                    {props.label}
                </Animated.Text>
            }
            <TextInput ref={inputRef} {...inputProps}
                style={styles.input}
                value={props.value}
                placeholder={placeholder}
                onChangeText={onChangeText}
                onFocus={onFocus}
                onBlur={onBlur}
                error={getFieldTouched(props.inputKey) && !!props.errors[props.inputKey]}
            />
            {
                withErrorText &&
                <Text style={styles.error} variant="error">
                    {getFieldTouched(props.inputKey) ? props.errors[props.inputKey] ?? ' ' : ' '}
                </Text>
            }
        </View>
    );
};
export default TextInputForm;
