import AppStore from "./AppStore";
import DiscountStore from "./DiscountStore";
import ProposalStore from "./ProposalStore";
import ServicesStore from "./ServicesStore";

const appStore = new AppStore();
const servicesStore = new ServicesStore(appStore);
const discountStore = new DiscountStore();
const proposalStore = new ProposalStore(appStore, servicesStore);

const stores = {
    appStore,
    servicesStore,
    discountStore,
    proposalStore,
};
export default stores;
export { AppStore, ServicesStore, DiscountStore, ProposalStore };

