import { CustomTheme } from '@theme';
import React from 'react';
import { StyleProp, View, ViewStyle } from 'react-native';
import { Checkbox as PaperCheckbox, TouchableRipple, useTheme } from 'react-native-paper';
import makeStyles from './css';

interface IProps {
    rootStyle?: StyleProp<ViewStyle>;
    status: "checked" | "unchecked" | "indeterminate";
    children?: React.ReactNode;
    primary?: boolean;
    error?: boolean;
    fieldName?: string;
    setFieldTouched?: (field: string, touched?: boolean) => void;
    setFieldValue?: (field: string, value: boolean) => void;
}

const Checkbox = (props: IProps) => {
    const theme = useTheme() as CustomTheme;
    const styles = makeStyles(theme);

    const primary = props.primary ?? true;

    const onCheckboxPress = () => {
        if (props.fieldName) {
            if (props.setFieldTouched) {
                props.setFieldTouched(props.fieldName, true);
            }
            if (props.setFieldValue) {
                const checked = props.status !== 'checked' ? true : false;
                props.setFieldValue(props.fieldName, checked);
            }
        }
    }

    return (
        <View style={[styles.root, props.rootStyle]}>
            <PaperCheckbox {...props}
                color={theme.colors[primary ? 'primary' : 'accent']}
                uncheckedColor={theme.colors[props.error ? 'error' : 'darkGray0']}
                onPress={onCheckboxPress} />
            <TouchableRipple style={styles.childrenContainer}
                onPress={onCheckboxPress} rippleColor={theme.colors.lightBlue}>
                <>
                    {props.children}
                </>
            </TouchableRipple>
        </View>
    );
};
export default Checkbox;
