import { Image, Text, ToggleButton } from '@atoms';
import { useProposal, useStore } from '@hooks';
import { DiscountTypesEnum } from '@models/Enums';
import { IOption } from '@models/IKeyValue';
import { IProposalEntry } from '@models/IProposalEntry';
import { Footer } from '@organisms';
import { baseHref, useNavigate } from '@routes';
import { AppStore, DiscountStore, ProposalStore } from '@stores';
import { Tooltip } from '@templates';
import { CustomTheme } from '@theme';
import CONSTANTS from '@utils/Constants';
import { useFormik } from 'formik';
import { observer } from 'mobx-react';
import React, { useEffect, useRef, useState } from 'react';
import { Animated, Easing, View } from 'react-native';
import { useTheme } from 'react-native-paper';
import * as Yup from 'yup';
import makeStyles from './css';

interface IProps {
}

const CarOwnerGender = observer((props: IProps) => {
    const appStore: AppStore = useStore('appStore');
    const proposalStore: ProposalStore = useStore('proposalStore');
    const discountStore: DiscountStore = useStore('discountStore');
    const theme = useTheme() as CustomTheme;
    const styles = makeStyles(theme);

    useEffect(() => {
        appStore.setLoading(false);
    }, []);

    const proposal = useProposal(proposalStore);

    const genderTypes: IOption[] = [
        { key: 1, value: 'אני בעל הרכב' },
        { key: 3, value: 'אני בעלת הרכב' },
    ];
    const genderError = 'אנחנו חייבים את המידע הזה כדי להמשיך להצעה';

    const validationSchema = Yup.object().shape({
        genderCode: Yup.number()
            .required(genderError)
            .oneOf(genderTypes.map(opt => parseInt(opt.key.toString())), genderError),
    });

    const navigate = useNavigate();
    const onSubmit = async (values: IProposalEntry) => {
        if (formik.isValid) {
            const female = values.genderCode === CONSTANTS.GENDER_CODES.FEMALE;
            const iconMoveTime = female ? 1500 : 0;
            if (iconMoveTime) {
                createAnimation(tooltipIconMoveToTopAnimator, iconMoveTime, 0, Easing.cubic);
            }
            setTimeout(async () => {
                if (female) {
                    discountStore.addDiscount(DiscountTypesEnum.Female);
                }
                appStore.setLoading(true);
                await proposalStore.setProposal(values);
                navigate(`/${baseHref}/stageOne/carOwnerPhoneNumber`);
            }, iconMoveTime);
        }
    };

    const formik = useFormik({
        initialValues: proposal,
        validationSchema,
        onSubmit: onSubmit,
        enableReinitialize: true,
    });

    const [formSubmited, setFormSubmited] = useState(false);
    const onSubmitButton = () => {
        setFormSubmited(true);
        formik.submitForm();
    }

    const getFieldTouched = (fieldName: string) => {
        return formSubmited || (formik.touched as any)[fieldName];
    }

    const tooltipStar1Animator = useRef(new Animated.Value(0)).current;
    const tooltipStar2Animator = useRef(new Animated.Value(0)).current;
    const tooltipIconOpacityAnimator = useRef(new Animated.Value(0)).current;
    const tooltipIconAnimator = useRef(new Animated.Value(0)).current;
    const tooltipIconMoveToTopAnimator = useRef(new Animated.Value(0)).current;

    const createAnimation = (animator: Animated.Value, duration: number, delay?: number, easing = Easing.ease) => {
        animator.setValue(0);
        animator.stopAnimation();
        Animated.timing(animator, {
            useNativeDriver: true,
            delay: delay ?? 0,
            duration: duration,
            toValue: 1,
            isInteraction: false,
            easing
        }).start();
    }

    const tooltipMoveTime = 400;
    useEffect(() => {
        if (formik.values.genderCode === CONSTANTS.GENDER_CODES.FEMALE) {
            createAnimation(tooltipStar1Animator, 200, tooltipMoveTime);
            createAnimation(tooltipStar2Animator, 300, tooltipMoveTime + 200);
            createAnimation(tooltipIconAnimator, 300, tooltipMoveTime + 100);
            createAnimation(tooltipIconOpacityAnimator, 300, tooltipMoveTime + 150);
        }
    }, [formik.values.genderCode]);

    const star1AnimStyle = {
        opacity: tooltipStar1Animator.interpolate({
            inputRange: [0, 1],
            outputRange: [0, 1]
        }),
    };
    const star1MoveIconAnimStyle = {
        transform: [
            {
                scale: tooltipIconMoveToTopAnimator.interpolate({
                    inputRange: [0, .15, .3, 1],
                    outputRange: [1, 0, 0, 0]
                }),
            }
        ]
    };
    const star2MoveIconAnimStyle = {
        transform: [
            {
                scale: tooltipIconMoveToTopAnimator.interpolate({
                    inputRange: [0, .15, .3, 1],
                    outputRange: [1, 1, 0, 0]
                }),
            }
        ]
    };
    const star2AnimStyle = {
        transform: [
            {
                scale: tooltipStar2Animator.interpolate({
                    inputRange: [0, .9, 1],
                    outputRange: [0, 1.1, 1]
                }),
            }
        ]
    };
    const tooltipIconAnimStyle = {
        opacity: tooltipIconOpacityAnimator.interpolate({
            inputRange: [0, 1],
            outputRange: [0, 1]
        }),
        transform: [
            {
                translateY: tooltipIconAnimator.interpolate({
                    inputRange: [0, .8, 1],
                    outputRange: [50, -16, 0]
                })
            }
        ]
    };

    const tooltipIconRedMoveHideAnimStyle = {
        opacity: tooltipIconMoveToTopAnimator.interpolate({
            inputRange: [0, .3, 1],
            outputRange: [1, 0, 0]
        }),
        transform: [
            {
                scale: tooltipIconMoveToTopAnimator.interpolate({
                    inputRange: [0, .2, 1],
                    outputRange: [1, .75, .75]
                })
            },
            {
                translateX: tooltipIconMoveToTopAnimator.interpolate({
                    inputRange: [0, .4, 1],
                    outputRange: [0, 10, 10]
                })
            }
        ]
    };
    const tooltipIconMoveToTopAnimStyle = {
        opacity: tooltipIconMoveToTopAnimator.interpolate({
            inputRange: [0, .3, 1],
            outputRange: [0, 1, 1]
        }),
        transform: [
            {
                scale: tooltipIconMoveToTopAnimator.interpolate({
                    inputRange: [0, .8, 1],
                    outputRange: [1, .75, .75]
                })
            },
            {
                translateX: tooltipIconMoveToTopAnimator.interpolate({
                    inputRange: [0, .4, 1],
                    outputRange: [0, 10, 10]
                })
            },
            {
                translateY: tooltipIconMoveToTopAnimator.interpolate({
                    inputRange: [0, .4, .9, 1],
                    outputRange: [0, 0, -780, -810]
                })
            }
        ]
    };

    return (
        <View style={styles.root}>
            <Text variant="h1">מי הבעלים של הרכב?</Text>
            <View style={styles.togglesContainer} accessibilityRole="radiogroup">
                {genderTypes.map(opt => (
                    <ToggleButton key={opt.key}
                        fieldName="genderCode"
                        value={opt.key}
                        setFieldTouched={formik.setFieldTouched}
                        setFieldValue={formik.setFieldValue}
                        checked={formik.values.genderCode === opt.key}
                        error={getFieldTouched('genderCode') && !!formik.errors.genderCode}
                        buttonProps={{
                            primary: true,
                            rootStyle: styles.genderCodeButton,
                            style: [styles.genderCodeButtonStyle, formik.values.genderCode === opt.key ? styles.genderCodeButtonStyleChecked : null],
                            contentStyle: styles.genderCodeButtonContent,
                            labelStyle: styles.genderCodeButtonLabel,
                            roundness: 6,
                            // accessibilityRole: 'radio',
                        }}>
                        <View style={styles.genderCodeButtonContentRadio}>
                            {
                                formik.values.genderCode === opt.key &&
                                <View style={styles.genderCodeButtonContentRadioChecked} />
                            }
                        </View>
                        <View style={styles.genderCodeButtonContentContainer}>
                            {
                                opt.key === CONSTANTS.GENDER_CODES.MALE &&
                                <Image style={styles.genderCodeButtonContentImage} source={require('@assets/images/male.png')} />
                            }
                            {
                                opt.key === CONSTANTS.GENDER_CODES.FEMALE &&
                                <Image style={styles.genderCodeButtonContentImage} source={require('@assets/images/female.png')} />
                            }
                            <Text style={styles.genderCodeButtonContentText} variant="h6">
                                {opt.value}
                            </Text>
                        </View>
                    </ToggleButton>
                ))}
            </View>
            <Text style={styles.error} variant="error">
                {getFieldTouched('genderCode') ? (formik.errors.genderCode ?? ' ') : ' '}
            </Text>
            <Footer onSubmitButton={onSubmitButton}>
                {
                    formik.values.genderCode === CONSTANTS.GENDER_CODES.FEMALE &&
                    <Tooltip withArrow={false} header="קיבלת מאיתנו הנחה נוספת!"
                        body="נהגות מעורבות פחות בתאונות ולכן נהנות מהנחה"
                        allowXAnim={false}>
                        <View style={styles.tooltipIcons}>
                            <Animated.Image style={[
                                styles.tooltipStar,
                                styles.tooltipStar1,
                                star1AnimStyle,
                                formSubmited ? star1MoveIconAnimStyle : null
                            ]} source={require('@assets/images/small-star.png')} />
                            <Animated.Image style={[
                                styles.tooltipStar,
                                styles.tooltipStar2,
                                star2AnimStyle,
                                formSubmited ? star2MoveIconAnimStyle : null
                            ]} source={require('@assets/images/small-star.png')} />
                            <Animated.Image style={[
                                styles.tooltipFemaleIcon,
                                tooltipIconAnimStyle,
                                formSubmited ? tooltipIconRedMoveHideAnimStyle : null
                            ]}
                                source={require('@assets/images/female-50.png')} />
                            <Animated.Image style={[
                                styles.tooltipFemaleIconDiscount,
                                tooltipIconMoveToTopAnimStyle
                            ]} source={require('@assets/images/female-36.png')} />
                        </View>
                    </Tooltip>
                }
            </Footer>
        </View>
    );
});
export default CarOwnerGender;
