import { Button } from '@atoms';
import { CustomTheme } from '@theme';
import React from 'react';
import { useTheme } from 'react-native-paper';
import { IButtonProps } from '../Button/IButtonProps';
import makeStyles from './css';

interface IProps {
    buttonProps?: IButtonProps;
    checked: boolean;
    children?: React.ReactNode;
    error?: boolean;
    fieldName?: string;
    value?: number | string;
    setFieldTouched?: (field: string, touched?: boolean) => void;
    setFieldValue?: (field: string, value: number | string | undefined) => void;
}

const ToggleButton = (props: IProps) => {
    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const onPress = () => {
        if (props.setFieldValue && props.fieldName) {
            if (props.setFieldTouched) {
                props.setFieldTouched(props.fieldName);
            }
            props.setFieldValue(props.fieldName, !props.checked ? props.value! : undefined);
        }
    }

    return (
        <Button {...props.buttonProps}
            mode={props.checked && !props.error ? 'contained' : 'outlined'}
            style={[
                props.buttonProps?.style,
                styles.style,
                props.error ? styles.errorStyle : null
            ]}
            contentStyle={[props.buttonProps?.contentStyle]}
            labelStyle={props.buttonProps?.labelStyle}
            textStyle={[
                styles.label,
                props.checked ? styles.checkedLabel : null
            ]}
            onPress={props.fieldName ? onPress : props.buttonProps?.onPress}>
            {props.children}
        </Button>
    );
};
export default ToggleButton;
