import { CustomTheme } from '@theme';
import React, { useEffect, useState } from 'react';
import { LayoutChangeEvent, View } from 'react-native';
import { useTheme } from 'react-native-paper';
import makeStyles from './css';

interface IProps {
    step: number;
    totalSteps: number;
}

const ProgressStep = (props: IProps) => {
    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const [activeValueWidth, setActiveValueWidth] = useState(0);
    const [activeLineLeftRounded, setActiveLineLeftRounded] = useState(false);

    const [lineWidth, setLineWidth] = useState(0);
    const onLineLayout = (event: LayoutChangeEvent) => {
        const { width } = event.nativeEvent.layout;
        setLineWidth(width);
    }

    useEffect(() => {
        const coloredWidth = lineWidth * props.step / props.totalSteps;
        if (coloredWidth > 0) {
            if (lineWidth - coloredWidth >= lineWidth) {
                setActiveLineLeftRounded(true);
            } else {
                setActiveLineLeftRounded(false);
            }
            setActiveValueWidth(lineWidth - coloredWidth);
        } else {
            setActiveValueWidth(lineWidth);
        }
    }, [lineWidth, props.step]);

    return (
        <View style={styles.root}>
            <View onLayout={onLineLayout} style={styles.line}>
                <View style={[
                    styles.activeValueLine,
                    activeValueWidth ? { right: activeValueWidth } : null,
                    activeLineLeftRounded ? styles.activeLineRounded : null
                ]}></View>
            </View>
            <View style={styles.title}></View>
        </View>
    );
};
export default ProgressStep;
