import { Image, Text } from '@atoms';
import { CustomTheme } from '@theme';
import React, { useEffect, useRef, useState } from 'react';
import { Animated, Easing, LayoutChangeEvent, View } from 'react-native';
import { useTheme } from 'react-native-paper';
import makeStyles from './css';

interface IProps {
    withArrow?: boolean;
    header?: string;
    body?: string;
    iconType?: 'lamp';
    children?: React.ReactNode;
    allowXAnim?: boolean;
    allowYAnim?: boolean;
}

const Tooltip = (props: IProps) => {
    const theme = useTheme() as CustomTheme;
    const styles = makeStyles(theme);

    const [width, setWidth] = useState(0);
    const [height, setHeight] = useState(0);
    const onLayout = (event: LayoutChangeEvent) => {
        const { width, height } = event.nativeEvent.layout;
        setWidth(width);
        setHeight(height);
    }

    const tooltipScaleAnimator = useRef(new Animated.Value(0)).current;
    const tooltipMoveAnimator = useRef(new Animated.Value(0)).current;
    const textAnimator = useRef(new Animated.Value(0)).current;
    const [animated, setAnimated] = useState(false);
    useEffect(() => {
        if (!animated && width && height) {
            setAnimated(true);
            tooltipScaleAnimator.stopAnimation();
            Animated.timing(tooltipScaleAnimator, {
                useNativeDriver: true,
                duration: 300,
                toValue: 1,
                isInteraction: false,
                easing: Easing.linear
            }).start();

            tooltipMoveAnimator.stopAnimation();
            Animated.timing(tooltipMoveAnimator, {
                useNativeDriver: true,
                duration: 300,
                toValue: 1,
                isInteraction: false,
                easing: Easing.linear
            }).start();

            textAnimator.stopAnimation();
            Animated.timing(textAnimator, {
                useNativeDriver: true,
                delay: 400,
                duration: 200,
                toValue: 1,
                isInteraction: false,
                easing: Easing.ease
            }).start();
        }
    }, [width, height]);

    const tooltipAnimScale = {
        transform: [
            {
                scale: tooltipScaleAnimator
            },
        ]
    };

    const allowXAnim = props.allowXAnim ?? true;
    const allowYAnim = props.allowYAnim ?? true;

    const tooltipAnimMove = {
        transform: [
            {
                translateX: tooltipMoveAnimator.interpolate({
                    inputRange: [0, 1],
                    outputRange: [allowXAnim ? width * .8 : 0, 0]
                })
            },
            {
                translateY: tooltipMoveAnimator.interpolate({
                    inputRange: [0, 1],
                    outputRange: [allowYAnim ? height * .8 : 0, 0]
                })
            }
        ]
    };

    const textAnimStyle = {
        opacity: textAnimator.interpolate({
            inputRange: [0, 1],
            outputRange: [0, 1]
        }),
    };

    const withArrow = props.withArrow ?? true;

    return (
        <Animated.View style={tooltipAnimMove}>
            <Animated.View onLayout={onLayout} style={[styles.root, tooltipAnimScale]}>
                {
                    withArrow &&
                    <View style={styles.arrowBorder}>
                        <View style={styles.arrow} />
                    </View>
                }
                {
                    props.iconType === 'lamp' &&
                    <Image style={styles.icon} source={require('@assets/images/lamp.png')} />
                }
                <Animated.View style={[styles.textContainer, textAnimStyle]}>
                    {
                        props.header &&
                        <Text style={styles.header} variant="h3">
                            {props.header}
                        </Text>
                    }
                    {
                        props.body &&
                        <Text style={styles.body} variant="h5">
                            {props.body}
                        </Text>
                    }
                </Animated.View>
                {props.children}
            </Animated.View>
        </Animated.View>
    );
};
export default Tooltip;
