import { Text } from '@atoms';
import { CustomTheme } from '@theme';
import React, { Ref, useState } from 'react';
import { LayoutChangeEvent, View } from 'react-native';
import { Button as PaperButton, useTheme } from 'react-native-paper';
import makeStyles from './css';
import { IButtonProps } from './IButtonProps';

const Button = React.forwardRef((props: IButtonProps, ref?: Ref<View>) => {
    const uppercase = props.uppercase ?? false;
    const primary = props.primary ?? false;

    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const [buttonHeight, setButtonHeight] = useState(0);
    const onLayout = (event: LayoutChangeEvent) => {
        if (!buttonHeight) {
            let { height } = event.nativeEvent.layout;
            if (height > 48 && height <= 62) {
                height = 62;
            } else if (height > 62 && height <= 76) {
                height = 76;
            } else if (height > 76 && height <= 90) {
                height = 90;
            } else {
                height = 48;
            }
            setButtonHeight(height);
        }
    }

    return (
        <View ref={ref} style={[styles.root, props.rootStyle]}>
            <PaperButton onLayout={onLayout} {...props}
                theme={props.roundness || props.roundness === 0 ? { roundness: props.roundness } : {}}
                color={theme.colors[primary ? 'primary' : 'accent']}
                uppercase={uppercase}
                style={[styles.style, props.style]}
                contentStyle={[
                    styles.content,
                    props.contentStyle,
                    // buttonHeight ? { flexBasis: buttonHeight } : null
                ]}
                labelStyle={[
                    styles.label,
                    props.labelStyle,
                ]}>
                <Text style={[
                    styles.text,
                    { color: theme.colors[primary ? 'text' : 'primary'] },
                    props.textStyle,
                ]}
                    numberOfLines={!props.multiline ? 1 : 0}>
                    {props.children}
                </Text>
            </PaperButton>
        </View>
    );
});
export default Button;