import { Button, IconButton, Image, LinkUrl, Text } from '@atoms';
import { useFirstRender } from '@hooks';
import { PrevPageButton } from '@molecules';
import { baseHref, useMatch } from '@routes';
import { CustomTheme } from '@theme';
import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from 'react';
import { Animated, Dimensions, Easing, StyleProp, View, ViewStyle } from 'react-native';
import { TouchableRipple, useTheme } from 'react-native-paper';
import makeStyles from './css';
import { IHeaderHandle } from './IHeaderHandle';

interface IProps {
    rootStyle?: StyleProp<ViewStyle>;
    menuIcon?: boolean;
    contactUsText?: string;
    headerVisible?: boolean;
    setAnimationEnded?: (value: boolean) => void;
    menuOpened?: (menuOpen: boolean) => void;
    contactUsOpened?: (contactUsOpened: boolean) => void;
    contactUsButtonStyle?: StyleProp<ViewStyle>;
}

const Header = forwardRef<IHeaderHandle, IProps>((props: IProps, ref) => {
    const theme = useTheme() as CustomTheme;
    const styles = makeStyles(theme);
    const deviceHeight = Dimensions.get('window').height;

    const match = useMatch({ path: `/${baseHref}/stageOne/insuranceType`, end: true });

    const firstRender = useFirstRender();
    const headerHeight = 72;
    const headerVisible = props.headerVisible ?? true;
    const contactUsText = props.contactUsText ?? 'יצירת קשר';

    const setMenuOpenWrapper = (value: boolean) => {
        if (props.menuOpened) {
            props.menuOpened(value);
        }
        setMenuOpen(value);
    }
    const setContactUsOpenedWrapper = (value: boolean) => {
        if (props.contactUsOpened) {
            props.contactUsOpened(value);
        }
        setContactUsOpened(value);
    }

    const [menuOpen, setMenuOpen] = useState(false);
    const [contactUsOpened, setContactUsOpened] = useState(false);
    const [menuAnimation, setMenuAnimation] = useState(false);
    const onMenuIconClick = () => {
        if (contactUsOpened) {
            setMenuAnimation(false);
            setContactUsOpenedWrapper(false);
        } else {
            setMenuAnimation(true);
            setMenuOpenWrapper(!menuOpen);
        }
        setMenuCollapseAnimationEnded(false);
    }

    const menuIconAnimator = useRef(new Animated.Value(0)).current;
    useEffect(() => {
        menuIconAnimator.stopAnimation();
        let toValue = menuOpen || contactUsOpened ? 1 : 0;
        if (!props.menuIcon) {
            toValue = 1;
        }
        Animated.timing(menuIconAnimator, {
            useNativeDriver: false,
            duration: props.menuIcon ? 500 : 0,
            toValue,
            isInteraction: false,
            easing: Easing.ease
        }).start(() => {
            if (!menuAnimation && props.setAnimationEnded) {
                props.setAnimationEnded(true);
            }
        });
    }, [menuOpen, contactUsOpened]);

    const animWaitLength = new Array(10).fill(0);
    const animWaitDegLength = new Array(10).fill('0deg');
    const menuIconTopAndBottomTranslateX = menuIconAnimator.interpolate({
        inputRange: [...animWaitLength, .5, 1],
        outputRange: [...animWaitLength, 4, 4]
    });
    const menuIconLineTop = {
        transform: [
            {
                rotate: menuIconAnimator.interpolate({
                    inputRange: [...animWaitLength, 1],
                    outputRange: [...animWaitDegLength, '45deg']
                })
            },
            { translateX: menuIconTopAndBottomTranslateX },
            {
                translateY: menuIconAnimator.interpolate({
                    inputRange: [...animWaitLength, .5, 1],
                    outputRange: [...animWaitLength, 4, 4]
                })
            },
        ]
    };
    const menuIconLineMiddle = {
        transform: [
            {
                translateX: menuIconAnimator.interpolate({
                    inputRange: [0, 1],
                    outputRange: [0, 50]
                })
            }
        ]
    };
    const menuIconLineBottom = {
        transform: [
            {
                rotate: menuIconAnimator.interpolate({
                    inputRange: [...animWaitLength, 1],
                    outputRange: [...animWaitDegLength, '-45deg']
                })
            },
            { translateX: menuIconTopAndBottomTranslateX },
            {
                translateY: menuIconAnimator.interpolate({
                    inputRange: [...animWaitLength, .5, 1],
                    outputRange: [...animWaitLength, -4, -4]
                })
            }
        ]
    };

    const menuCollapseAnimator = useRef(new Animated.Value(0)).current;
    const [menuCollapseAnimationEnded, setMenuCollapseAnimationEnded] = useState(true);
    useEffect(() => {
        if (!firstRender) {
            menuCollapseAnimator.stopAnimation();
            setMenuCollapseAnimationEnded(false);
            Animated.timing(menuCollapseAnimator, {
                useNativeDriver: false,
                duration: contactUsOpened || !props.menuIcon ? 0 : 1000,
                toValue: menuOpen ? 1 : 0,
                isInteraction: false,
                easing: Easing.ease
            }).start(() => {
                setMenuCollapseAnimationEnded(true);
            });
        }
    }, [menuOpen, contactUsOpened]);
    const menuIconColor = {
        backgroundColor: menuCollapseAnimator.interpolate({
            inputRange: [0, .2, 1],
            outputRange: [theme.colors.white, theme.colors.primary, theme.colors.primary]
        })
    };
    const contactUsMenuIconColor = {
        backgroundColor: menuCollapseAnimator.interpolate({
            inputRange: [0, 1],
            outputRange: [theme.colors.white, theme.colors.white]
        })
    };
    const menuCollapse = {
        maxHeight: menuCollapseAnimator.interpolate({
            inputRange: [0, 1],
            outputRange: [headerHeight, deviceHeight]
        }),
        opacity: menuCollapseAnimator.interpolate({
            inputRange: [0, .3, 1],
            outputRange: [0, 1, 1]
        }),
    };
    const menuOverlayOpacity = {
        opacity: menuCollapseAnimator.interpolate({
            inputRange: [0, 1],
            outputRange: [0, .33]
        }),
    };

    const onContactUs = () => {
        if (menuOpen) {
            setMenuOpenWrapper(false);
        }
        setContactUsOpenedWrapper(true);
    }

    useImperativeHandle(ref, () => ({
        onContactUs() {
            onContactUs();
        },
        headerHeight,
    }));

    return (
        <>
            {headerVisible &&
                <View style={styles.root}>
                    {
                        !props.menuIcon && !contactUsOpened &&
                        <PrevPageButton style={{ opacity: match ? 0 : 1 }}
                            disabled={!!match} />
                    }
                    {
                        (props.menuIcon || contactUsOpened) &&
                        <IconButton rootStyle={styles.menuIconRoot} iconStyle={styles.menuIcon}
                            icon={require('@assets/images/hamburger-white-icon.png')}
                            buttonProps={{
                                onPress: () => { },
                                style: styles.menuButton,
                                contentStyle: styles.menuContent,
                                labelStyle: styles.menuLabel,
                            }} />
                    }
                    <View style={styles.logoContainer}>
                        <Image style={styles.logo} source={require('@assets/images/logo.png')} />
                    </View>
                    <Button mode="outlined" onPress={onContactUs}
                        style={[styles.contactUsStyle, props.contactUsButtonStyle]}
                        contentStyle={styles.contactUsContent}
                        labelStyle={styles.contactUsLabel}>
                        <Text style={styles.contactUsText} variant="h5">{contactUsText}</Text>
                    </Button>
                </View>
            }
            {
                ((headerVisible && (props.menuIcon || contactUsOpened)) || menuOpen || !menuCollapseAnimationEnded) &&
                <View style={styles.menuCustomButtonContainer}>
                    <TouchableRipple onPress={onMenuIconClick}
                        rippleColor="transparent"
                        style={styles.menuCustom}>
                        <View style={styles.menuLineContainer}>
                            <Animated.View style={[
                                styles.menuLine, styles.menuLine1,
                                !contactUsOpened ? menuIconColor : contactUsMenuIconColor,
                                menuIconLineTop
                            ]} />
                            <Animated.View style={[
                                styles.menuLine, styles.menuLine2,
                                !contactUsOpened ? menuIconColor : contactUsMenuIconColor,
                                menuIconLineMiddle
                            ]} />
                            <Animated.View style={[
                                styles.menuLine, styles.menuLine3,
                                !contactUsOpened ? menuIconColor : contactUsMenuIconColor,
                                menuIconLineBottom
                            ]} />
                        </View>
                    </TouchableRipple>
                </View>
            }
            {
                (menuOpen || !menuCollapseAnimationEnded) &&
                <>
                    <Animated.View style={[styles.menuOverlay, menuOverlayOpacity]} />
                    <Animated.View style={[styles.menuContainer, menuCollapse]}>
                        <LinkUrl style={[styles.menuContainerLink, styles.menuContainerLinkWithoutMargin]}
                            url="https://myinfo.fnx.co.il/">
                            אזור אישי
                        </LinkUrl>
                        <LinkUrl style={styles.menuContainerLink}
                            url="">
                            הגשת תביעה
                        </LinkUrl>
                        <LinkUrl style={styles.menuContainerLink}
                            onPress={onContactUs}>
                            צור קשר
                        </LinkUrl>
                        <View style={styles.menuDivider} />
                        <Text style={[styles.menuContainerLink, styles.menuContainerLinkWithoutMargin]} variant="h3">
                            ביטוח רכב
                        </Text>
                        <LinkUrl style={styles.menuContainerLink}
                            url="https://smart.fnx.co.il/travel/">
                            ביטוח נסיעות לחו״ל
                        </LinkUrl>
                        <LinkUrl style={styles.menuContainerLink}
                            url="https://smart.fnx.co.il/home/">
                            ביטוח דירה
                        </LinkUrl>
                        <LinkUrl style={styles.menuContainerLink}
                            url="https://smart.fnx.co.il/mortgage/">
                            ביטוח משכנתא
                        </LinkUrl>
                        <LinkUrl style={styles.menuContainerLink}
                            url="https://smart.fnx.co.il/pets/">
                            ביטוח חיות מחמד
                        </LinkUrl>
                    </Animated.View>
                </>
            }
        </>
    );
});
export default Header;

