import { CarOwnerFirstName, CarOwnerGender, CarOwnerPhoneNumber, InsuranceType, Landing } from "@screens";
import StageOneModule from "@screens/Modules/StageOneModule";
import React, { useEffect } from "react";
import { BackHandler } from "react-native";
import { Navigate, Route, Routes, useNavigate } from "./routeImporter";

// const StageOneModule = React.lazy(() => import('@screens/Modules/stageOneModule'));

const baseHref = 'car';

const AppRoutes = () => {
    const navigate = useNavigate();

    const backAction = (): boolean | null | undefined => {
        navigate(-1);
        return true;
    }

    useEffect(() => {
        BackHandler.addEventListener('hardwareBackPress', backAction);
        return () => BackHandler.removeEventListener('hardwareBackPress', backAction);
    }, []);

    return (
        <Routes>
            <Route path={baseHref}>
                <Route path="" element={<Landing />} />
                {/* <Route path="stageOne" element={
                    <React.Suspense fallback={<AppLoading />}>
                        <StageOneModule />
                    </React.Suspense>
                }> */}
                <Route path="stageOne" element={<StageOneModule />}>
                    <Route path="" element={<Navigate to='insuranceType' />} />
                    <Route path="insuranceType" element={<InsuranceType />} />
                    <Route path="carOwnerFirstName" element={<CarOwnerFirstName />} />
                    <Route path="carOwnerGender" element={<CarOwnerGender />} />
                    <Route path="carOwnerPhoneNumber" element={<CarOwnerPhoneNumber />} />
                </Route>
            </Route>
            <Route path="*" element={<Navigate to={baseHref} />} />
        </Routes>
    );
};
export default AppRoutes;
export { Link, Outlet, Router, useMatch, useNavigate } from './routeImporter';
export { baseHref };

