import { Text } from '@atoms';
import { useProposal, useStore } from '@hooks';
import { IProposalEntry } from '@models/IProposalEntry';
import { TextInputForm } from '@molecules';
import { Footer } from '@organisms';
import { baseHref, useNavigate } from '@routes';
import { AppStore, ProposalStore } from '@stores';
import { Tooltip } from '@templates';
import { CustomTheme } from '@theme';
import { useFormik } from 'formik';
import { observer } from 'mobx-react';
import React, { useEffect, useState } from 'react';
import { View } from 'react-native';
import { useTheme } from 'react-native-paper';
import * as Yup from 'yup';
import makeStyles from './css';

interface IProps {
}

const CarOwnerFirstName = observer((props: IProps) => {
    const appStore: AppStore = useStore('appStore');
    const proposalStore: ProposalStore = useStore('proposalStore');
    const theme = useTheme() as CustomTheme;
    const styles = makeStyles(theme);

    useEffect(() => {
        appStore.setLoading(false);
    }, []);

    const proposal = useProposal(proposalStore);

    const validationSchema = Yup.object().shape({
        firstName: Yup.string()
            .required('נשמח לדעת את השם כדי להתחיל :)'),
        lastName: Yup.string()
            .required('נשמח לדעת את שם המשפחה כדי להתחיל :)'),
    });

    const navigate = useNavigate();
    const onSubmit = async (values: IProposalEntry) => {
        if (formik.isValid) {
            appStore.setLoading(true);
            await proposalStore.setProposal(values);
            navigate(`/${baseHref}/stageOne/carOwnerGender`);
        }
    };

    const formik = useFormik({
        initialValues: proposal,
        validationSchema,
        onSubmit: onSubmit,
        enableReinitialize: true,
    });

    const [formSubmited, setFormSubmited] = useState(false);
    const onSubmitButton = () => {
        setFormSubmited(true);
        formik.submitForm();
    }

    return (
        <View style={styles.root}>
            <Text variant="h1">
                נעים להכיר :){'\n'}
                מה השם שלך?
            </Text>
            <TextInputForm inputKey="firstName"
                value={formik.values.firstName}
                label="שם פרטי של בעל הרכב"
                setFieldValue={formik.setFieldValue}
                setFieldTouched={formik.setFieldTouched}
                touched={formik.touched} errors={formik.errors}
                inputProps={{
                    textContentType: 'givenName',
                    autoCompleteType: 'name',
                }}
                formSubmited={formSubmited} />
            <TextInputForm inputKey="lastName"
                value={formik.values.lastName}
                label="שם המשפחה של בעל הרכב"
                setFieldValue={formik.setFieldValue}
                setFieldTouched={formik.setFieldTouched}
                touched={formik.touched} errors={formik.errors}
                inputProps={{
                    textContentType: 'familyName',
                    autoCompleteType: 'name',
                }}
                formSubmited={formSubmited} />
            <Tooltip body="ההצעה שלנו מיועדת למי שהרכב בבעלותו והביטוח יהיה על שמו"
                iconType="lamp" />
            <Footer onSubmitButton={onSubmitButton} />
        </View>
    );
});
export default CarOwnerFirstName;
