import { IProposalEntry } from '@models/IProposalEntry';
import proposalEntry from '@models/ProposalEntry';
import { AppStore, ServicesStore } from '@stores';
import { action, makeAutoObservable, observable, runInAction } from 'mobx';

class ProposalStore {
    @observable model = proposalEntry;

    private appStore: AppStore;
    private servicesStore: ServicesStore;

    constructor(appStore: AppStore, servicesStore: ServicesStore) {
        this.appStore = appStore;
        this.servicesStore = servicesStore;
        makeAutoObservable(this);
    }

    @action getProposal = async () => {
        let validUntil = new Date(new Date().getTime() - this.appStore.cacheTimeSec * 1000 * 60);
        const model = await this.servicesStore.cache.get<IProposalEntry>('proposal', validUntil);
        if (model) {
            runInAction(() => {
                this.model = model;
            });
        }
    }

    @action setProposal = async (entry: IProposalEntry) => {
        await this.servicesStore.cache.add('proposal', entry);
        runInAction(() => {
            this.model = entry;
        });
    }
}
export default ProposalStore;