import { DiscountTypesEnum } from '@models/Enums';
import { IDiscountEntry } from '@models/IDiscountEntry';
import { IProposalEntry } from '@models/IProposalEntry';
import CONSTANTS from '@utils/Constants';
import { action, makeAutoObservable, observable } from 'mobx';

class DiscountStore {
    @observable discounts = new Array<IDiscountEntry>();

    private allDiscounts: IDiscountEntry[] = [
        { icon: require('@assets/images/icon-36.png'), title: 'online discount', type: DiscountTypesEnum.Online },
        { icon: require('@assets/images/female-36.png'), title: 'female discount', type: DiscountTypesEnum.Female },
    ];

    constructor() {
        makeAutoObservable(this);
    }

    @action addDiscount(type: DiscountTypesEnum) {
        const foundedDiscount = this.discounts.find(d => d.type === type);
        if (!foundedDiscount) {
            const addDiscount = this.allDiscounts.find(d => d.type === type);
            if (addDiscount) {
                this.discounts.push(addDiscount);
            }
        }
    }

    @action removeDiscount(type: DiscountTypesEnum) {
        const foundedDiscount = this.discounts.find(d => d.type === type);
        if (foundedDiscount) {
            this.discounts.splice(this.discounts.indexOf(foundedDiscount), 1);
        }
    }

    @action fillDiscounts(proposal: IProposalEntry, screenName: string) {
        this.discounts = [];
        this.addDiscount(DiscountTypesEnum.Online);
        if (proposal.genderCode === CONSTANTS.GENDER_CODES.FEMALE && screenName !== 'carOwnerGender') {
            this.addDiscount(DiscountTypesEnum.Female);
        }
    }
}
export default DiscountStore;