import { useStore } from '@hooks';
import { Header, IHeaderHandle, SideBarMenu, StepProgressBar } from '@organisms';
import { baseHref } from '@routes';
import { AppStore, DiscountStore, ProposalStore } from '@stores';
import { AppLoading, ContactUs } from '@templates';
import { CustomTheme } from '@theme';
import Constants from 'expo-constants';
import { observer } from 'mobx-react';
import React, { useEffect, useRef, useState } from 'react';
import { Dimensions, LayoutChangeEvent, SafeAreaView, ScrollView, View } from 'react-native';
import { useTheme } from 'react-native-paper';
import { useLocation, useNavigate } from 'react-router';
import makeStyles from './css';

interface IProps {
    children?: React.ReactNode;
}

const Layout = observer((props: IProps) => {
    const appStore: AppStore = useStore('appStore');
    const discountStore: DiscountStore = useStore('discountStore');
    const proposalStore: ProposalStore = useStore('proposalStore');

    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const navigate = useNavigate();
    const location = useLocation();

    const getScreenName = () => {
        const paths = location.pathname.split('/');
        if (!paths.length) {
            return '';
        }
        return paths[paths.length - 1];
    }

    useEffect(() => {
        if (proposalStore.model) {
            discountStore.fillDiscounts(proposalStore.model, getScreenName());
        }
    }, [location]);

    useEffect(() => {
        const asyncFuncs = async () => {
            await proposalStore.getProposal();
            discountStore.fillDiscounts(proposalStore.model, getScreenName());
            if (!proposalStore.model.insuranceType) {
                navigate(`/${baseHref}/stageOne/insuranceType`);
            }
        };
        asyncFuncs();

        const deviceSizeChangeListener = () => {
            const { height } = Dimensions.get('window');
            setScrollMaxHeight(height - Constants.statusBarHeight - headerHeight());
        };
        deviceSizeChangeListener();

        Dimensions.addEventListener('change', deviceSizeChangeListener);
        return () => {
            Dimensions.removeEventListener('change', deviceSizeChangeListener);
        };
    }, []);

    const headerHeight = () => {
        return headerRef.current?.headerHeight ?? 0;
    };

    const headerRef = useRef<IHeaderHandle>(null);
    const [contactUsOpened, setContactUsOpened] = useState(false);
    const [scrollMaxHeight, setScrollMaxHeight] = useState<number | null>(null);

    const onLayout = (event: LayoutChangeEvent) => {
        const { height } = event.nativeEvent.layout;
        appStore.setLayoutHeight(height);
    }

    return <>
        {appStore.loading && <AppLoading />}
        <SafeAreaView onLayout={onLayout} style={[styles.root, { marginTop: Constants.statusBarHeight }]}>
            <Header ref={headerRef} contactUsOpened={setContactUsOpened}
                contactUsButtonStyle={styles.contactUsButtonStyle} />
            <ScrollView style={[
                styles.scrollContainer,
                scrollMaxHeight ? { maxHeight: scrollMaxHeight } : null,
                { marginTop: headerHeight() }]}
                contentContainerStyle={styles.scrollContentContainer}
                scrollEventThrottle={2}>
                <StepProgressBar />
                <SideBarMenu />
                <View style={styles.content}>
                    {props.children}
                </View>
            </ScrollView>
            {
                contactUsOpened &&
                <ContactUs top={headerHeight()}
                    header="נשמח לשמוע ממך :)"
                    submitButtonBottom
                    submitButtonLabelStyle={styles.contactUsSubmitButtonLabel} />
            }
        </SafeAreaView>
    </>;
});
export default Layout;
