import { Text, ToggleButton } from '@atoms';
import { IContactUsEntry } from '@models/IContactUsEntry';
import { IOption } from '@models/IKeyValue';
import { TextInputForm } from '@molecules';
import { CustomTheme } from '@theme';
import { useFormik } from 'formik';
import React, { forwardRef, useImperativeHandle } from 'react';
import { View } from 'react-native';
import { useTheme } from 'react-native-paper';
import * as Yup from 'yup';
import makeStyles from './css';

interface IProps {
}

interface IHandle {
    submitForm: () => void;
};

const ContactUsForm = forwardRef<IHandle, IProps>((props: IProps, ref) => {
    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const initialValues: IContactUsEntry = {
        subject: undefined,
        name: '',
        lastName: '',
        phone: '',
    };

    const subjects: IOption[] = [
        { key: 6, value: 'הצעת מחיר' },
        { key: 44, value: 'חידוש ביטוח' },
        { key: 99, value: 'שירות' }
    ];

    const validationSchema = Yup.object().shape({
        subject: Yup.number()
            .required('שדה חובה')
            .oneOf(subjects.map(opt => parseInt(opt.key.toString())), 'שדה חובה'),
        name: Yup.string()
            .required(),
        lastName: Yup.string()
            .required(),
        phone: Yup.string()
            .required(),
    });

    const onSubmit = async (values: IContactUsEntry) => {
        if (formik.isValid) {
        }
    };

    useImperativeHandle(ref, () => ({
        submitForm() {
            formik.submitForm();
        }
    }));

    const formik = useFormik({
        initialValues,
        validationSchema,
        onSubmit: onSubmit,
    });

    const onSubject = (opt: IOption) => {
        const value = formik.values.subject !== opt.key ? opt.key : undefined;
        formik.setFieldValue('subject', value);
    };

    return (
        <View style={styles.root}>
            <View style={styles.subjectContainer}>
                {subjects.map(opt => (
                    <ToggleButton key={opt.key}
                        checked={formik.values.subject === opt.key}
                        error={!!formik.errors.subject}
                        buttonProps={{
                            primary: true,
                            rootStyle: [
                                styles.subjectButton,
                                (styles as any)[`subjectButton_${opt.key}`],
                                !!formik.errors.subject ? (styles as any)[`subjectButtonError_${opt.key}`] : null
                            ],
                            contentStyle: styles.subjectButtonContent,
                            onPress: () => { onSubject(opt) }
                        }}>
                        {opt.value}
                    </ToggleButton>
                ))}
            </View>
            {
                !!formik.errors.subject &&
                <Text style={styles.subjectError}>{formik.errors.subject}</Text>
            }
            <TextInputForm inputKey="name"
                withErrorText={false}
                value={formik.values.name}
                placeholder="שם פרטי"
                setFieldValue={formik.setFieldValue}
                setFieldTouched={formik.setFieldTouched}
                touched={formik.touched} errors={formik.errors}
                inputProps={{
                    textContentType: 'givenName',
                    autoCompleteType: 'name',
                }} />
            <TextInputForm inputKey="lastName"
                withErrorText={false}
                value={formik.values.lastName}
                placeholder="שם משפחה"
                setFieldValue={formik.setFieldValue}
                setFieldTouched={formik.setFieldTouched}
                touched={formik.touched} errors={formik.errors}
                inputProps={{
                    textContentType: 'familyName',
                    autoCompleteType: 'name',
                }} />
            <TextInputForm inputKey="phone"
                withErrorText={false}
                value={formik.values.phone}
                placeholder="מספר טלפון"
                setFieldValue={formik.setFieldValue}
                setFieldTouched={formik.setFieldTouched}
                touched={formik.touched} errors={formik.errors}
                inputProps={{
                    textContentType: 'telephoneNumber',
                    autoCompleteType: 'tel',
                    keyboardType: 'phone-pad',
                }} />
        </View>
    );
});
export default ContactUsForm;
export { IHandle as IContactUsFormHandle };

