import proposalEntry from '@models/ProposalEntry';
import { ProposalStore } from '@stores';
import { useEffect, useState } from 'react';

const useProposal = (proposalStore: ProposalStore) => {
    const [proposal, setProposal] = useState(proposalEntry);

    useEffect(() => {
        setProposal(proposalStore.model);
    }, [proposalStore.model]);

    return proposal;
};
export default useProposal;