import { ProgressStep } from '@atoms';
import { baseHref, useMatch } from '@routes';
import { CustomTheme } from '@theme';
import React, { useEffect, useState } from 'react';
import { View } from 'react-native';
import { useTheme } from 'react-native-paper';
import makeStyles from './css';

interface IProps {
}

const StepProgressBar = (props: IProps) => {
    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const [step, setStep] = useState(1);
    const stageOneTotalSteps = 17;
    const stageTwoTotalSteps = 1;
    const stageThreeTotalSteps = 8;
    const stageFourTotalSteps = 2;

    const mInsuranceType = useMatch({ path: `/${baseHref}/stageOne/insuranceType`, end: true });
    const mCarOwnerFirstName = useMatch({ path: `/${baseHref}/stageOne/carOwnerFirstName`, end: true });
    const mCarOwnerGender = useMatch({ path: `/${baseHref}/stageOne/carOwnerGender`, end: true });
    const mCarOwnerPhoneNumber = useMatch({ path: `/${baseHref}/stageOne/carOwnerPhoneNumber`, end: true });
    useEffect(() => {
        let stepByUrl = 0; // default mInsuranceType
        if (mCarOwnerFirstName) {
            stepByUrl = 1;
        } else if (mCarOwnerGender) {
            stepByUrl = 2;
        } else if (mCarOwnerPhoneNumber) {
            stepByUrl = 3;
        }
        setStep(stepByUrl);
    }, [
        mInsuranceType, mCarOwnerFirstName, mCarOwnerGender, mCarOwnerPhoneNumber
    ]);

    const getProgressStep = (index: number, step: number, totalSteps: number) => {
        let calcStep = step;
        if (index >= 2) {
            calcStep -= stageOneTotalSteps;
        }
        if (index >= 3) {
            calcStep -= stageTwoTotalSteps;
        }
        if (index >= 4) {
            calcStep -= stageThreeTotalSteps;
        }
        return <ProgressStep step={calcStep} totalSteps={totalSteps} />;
    }

    return (
        <View style={styles.root}>
            {getProgressStep(1, step, stageOneTotalSteps)}
            {getProgressStep(2, step, stageTwoTotalSteps)}
            {getProgressStep(3, step, stageThreeTotalSteps)}
            {getProgressStep(4, step, stageFourTotalSteps)}
        </View>
    );
};
export default StepProgressBar;
