import { CustomTheme } from '@theme';
import React, { forwardRef } from 'react';
import { TextInput as PaperTextInput, useTheme } from 'react-native-paper';
import { TextInputProps } from 'react-native-paper/lib/typescript/components/TextInput/TextInput';
import makeStyles from './css';

interface IProps extends TextInputProps {
    roundness?: number;
}

const TextInput = forwardRef((props: IProps, ref: any) => {
    const theme = useTheme() as CustomTheme;
    const styles = makeStyles(theme);

    const roundness = { roundness: props.roundness ?? theme.inputRoundness };
    const backgroundColor = { colors: { background: theme.colors.white } };
    const additionalTheme = { ...roundness, ...backgroundColor };

    return (
        <PaperTextInput ref={ref} {...props} dense
            style={[styles.input, props.style]}
            selectTextOnFocus
            outlineColor={theme.colors.primary66}
            activeOutlineColor={theme.colors.inputFocus}
            placeholderTextColor={theme.colors.primary}
            theme={additionalTheme} />
    );
});
export default TextInput;
