import AppRoutes, { Router } from '@routes';
import stores from '@stores';
import { AppLoading } from '@templates';
import theme from '@theme';
import { useFonts } from 'expo-font';
import { useKeepAwake } from 'expo-keep-awake';
import { StatusBar } from 'expo-status-bar';
import { Provider } from 'mobx-react';
import React from 'react';
import { I18nManager, LogBox } from 'react-native';
import { Provider as PaperProvider } from 'react-native-paper';

LogBox.ignoreLogs([
  'Require cycle:'
]);

I18nManager.allowRTL(true);
I18nManager.forceRTL(true);

// SplashScreen.preventAutoHideAsync();

export default () => {
  let [fontsLoaded] = useFonts({
    'ploni': require('@assets/fonts/ploni-regular-aaa.ttf'),
    'ploni-medium': require('@assets/fonts/ploni-medium-aaa.ttf'),
    'ploni-bold': require('@assets/fonts/ploni-bold-aaa.ttf'),
  });

  useKeepAwake();

  const [globalStore] = React.useState(stores);

  if (!fontsLoaded) {
    return <AppLoading />;
  } else {
    return (
      <PaperProvider theme={theme}>
        <Provider store={globalStore}>
          <Router>
            <AppRoutes />
          </Router>
          <StatusBar style="light" backgroundColor={theme.colors.primary} />
        </Provider>
      </PaperProvider>
    );
  }
}
