import { Button, IconButton, Image, LinkUrl, RadioButton, Text } from '@atoms';
import { useStore } from '@hooks';
import { Header, IHeaderHandle } from '@organisms';
import { baseHref } from '@routes';
import { AppStore } from '@stores';
import { AppLoading, ContactUs } from '@templates';
import { CustomTheme } from '@theme';
import Constants from 'expo-constants';
import { observer } from 'mobx-react';
import React, { useEffect, useRef, useState } from 'react';
import { Animated, Dimensions, Easing, ImageResizeMode, ImageSourcePropType, LayoutChangeEvent, Linking, NativeScrollEvent, NativeSyntheticEvent, SafeAreaView, ScaledSize, ScrollView, StyleProp, TextStyle, View } from 'react-native';
import { Divider, useTheme } from 'react-native-paper';
import { useNavigate } from 'react-router';
import makeStyles from './css';

const Landing = observer(() => {
    const appStore: AppStore = useStore('appStore');
    const theme = useTheme() as CustomTheme;
    const deviceWidth = Dimensions.get('window').width;
    const [styles, setStyles] = useState(makeStyles(theme, deviceWidth));

    const [stickyVisible, setStickyVisible] = useState<boolean>(false);
    const [stickyVisibleRuleY, setStickyVisibleRuleY] = useState<number | null>(null);
    const [deviceWindow, setDeviceWindow] = useState<ScaledSize | null>(null);
    const [scrollMaxHeight, setScrollMaxHeight] = useState(0);

    useEffect(() => {
        setTimeout(() => {
            appStore.setLoading(false);
        }, 3000);//300);//

        const sizeChangeListener = () => {
            const dWindow = Dimensions.get('window');
            setStyles(makeStyles(theme, dWindow.width));
            setDeviceWindow(dWindow);
            setScrollMaxHeight(dWindow.height + Constants.statusBarHeight);
        };
        sizeChangeListener();

        Dimensions.addEventListener('change', sizeChangeListener);
        return () => {
            Dimensions.removeEventListener('change', sizeChangeListener);
        };
    }, []);

    const headerHeight = () => {
        return headerRef.current?.headerHeight ?? 0;
    };

    const scrollRef = useRef<ScrollView>(null);
    const onScroll = (event: NativeSyntheticEvent<NativeScrollEvent>) => {
        const { y } = event.nativeEvent.contentOffset;
        if (stickyVisibleRuleY && y > stickyVisibleRuleY + headerHeight() * 3) {
            if (!stickyVisible) {
                setStickyVisible(true);
                setScrollMaxHeight(deviceWindow?.height ?? 0 + Constants.statusBarHeight - headerHeight());
                // scrollRef.current?.scrollTo({ y: y + headerHeight(), animated: false });
            }
        } else {
            if (stickyVisible) {
                setStickyVisible(false);
                setScrollMaxHeight(deviceWindow?.height ?? 0 + Constants.statusBarHeight);
                // scrollRef.current?.scrollTo({ y: y - headerHeight(), animated: false });
            }
        }
    }

    const carAnimator = useRef(new Animated.Value(0)).current;
    const [animationEnded, setAnimationEnded] = useState(false);
    useEffect(() => {
        Animated.timing(carAnimator, {
            useNativeDriver: true,
            duration: 1300,
            toValue: 1,
            isInteraction: false,
            easing: Easing.ease
        }).start(() => { setAnimationEnded(true) });
    }, []);

    const carMovement = {
        transform: [{
            translateX: carAnimator.interpolate({
                inputRange: [0, 1],
                outputRange: [-377, -65],
            })
        }]
    };

    const onProcessButtonLayout = (event: LayoutChangeEvent) => {
        const { y, height } = event.nativeEvent.layout;
        setStickyVisibleRuleY(y + height + Constants.statusBarHeight);
    }

    const navigate = useNavigate();
    const onProccess = () => {
        appStore.setLoading(true, 0);
        navigate(`/${baseHref}/stageOne/insuranceType`);
    }

    const getInfoBlock = (imageSource: ImageSourcePropType, textChildren: React.ReactNode,
        imageResizeMode: ImageResizeMode | undefined = undefined, textStyle: StyleProp<TextStyle> = null) => {
        return (
            <View style={styles.infoBlock}>
                <View style={styles.infoBlockContent}>
                    <Image style={styles.infoBlockImage}
                        source={imageSource} resizeMode={imageResizeMode} />
                    <Text style={[styles.infoBlockText, textStyle]}>
                        {textChildren}
                    </Text>
                </View>
            </View>
        );
    }

    const getCoveragesInfo = (textChildren: React.ReactNode) => {
        return (
            <View style={styles.coveragesInfoBlock}>
                <Image style={styles.coveragesInfoImage}
                    source={require('@assets/images/arrow-green-icon.png')} />
                <Text style={styles.coveragesInfoText} variant="h6">
                    {textChildren}
                </Text>
            </View>
        );
    }

    const [expandedQuestionValue, setExpandedQuestionValue] = useState('');
    const onQuestionToggle = (value: string) => {
        if (expandedQuestionValue === value) {
            setExpandedQuestionValue('');
        } else {
            setExpandedQuestionValue(value);
        }
    }

    const checkedImage = <Image style={styles.questionIcon} source={require('@assets/images/icon-blue-minus.png')} />
    const uncheckedImage = <Image style={styles.questionIcon} source={require('@assets/images/icon-blue-plus.png')} />
    const getQuestion = (value: string, title: string, description: string) => {
        return (
            <View style={styles.questionBlock}>
                <RadioButton value={value}
                    title={title}
                    status={expandedQuestionValue === value ? 'checked' : 'unchecked'}
                    onValueChange={onQuestionToggle}
                    collapsedDescription={description}
                    descriptionStyle={styles.questionDescriptionText}
                    checkedIcon={checkedImage}
                    uncheckedIcon={uncheckedImage} />
            </View>
        );
    }

    const onPhoneClick = () => {
        const phone = `tel: ${appStore.contactPhone}`;
        Linking.openURL(phone);
    }

    const onEmailClick = () => {
        const phone = `mailto: ${appStore.contactEmail}`;
        Linking.openURL(phone);
    }

    const openLink = async (url: string) => {
        if (await Linking.canOpenURL(url)) {
            Linking.openURL(url);
        }
    }

    const headerRef = useRef<IHeaderHandle>(null);
    const onContactUs = () => {
        headerRef.current?.onContactUs();
    }

    const [menuOpen, setMenuOpen] = useState(false);
    const [contactUsOpened, setContactUsOpened] = useState(false);

    const rtlStyle: StyleProp<TextStyle> = { direction: 'rtl' };

    const getButtonProcessFontSize = () => {
        const maxFontSize = theme.fonts.defaultFontSize * 1.25;
        const recFontSize = (deviceWidth - 150) / 250 * maxFontSize;
        return Math.min(maxFontSize, recFontSize);
    }

    return <>
        {appStore.loading && <AppLoading />}
        <SafeAreaView style={[styles.root, { marginTop: Constants.statusBarHeight }]}>
            <Header ref={headerRef} menuIcon
                headerVisible={stickyVisible}
                menuOpened={setMenuOpen}
                contactUsOpened={setContactUsOpened}
                contactUsText="צור קשר" />
            <ScrollView ref={scrollRef}
                onScroll={onScroll}
                focusable={!menuOpen && !contactUsOpened}
                scrollEventThrottle={2}
                scrollEnabled={animationEnded}
                style={[styles.scrollContainer, scrollMaxHeight ? { maxHeight: scrollMaxHeight } : null]}>
                <View style={styles.header}>
                    <Image style={styles.headerBg}
                        source={require('@assets/images/header-mobile.png')} />
                    <Image style={styles.logo}
                        source={require('@assets/images/phoenix-smart.png')} />
                    <Text variant="h1"
                        style={styles.headerH1}
                        accessibilityRole="header"
                        accessible>
                        ביטוח רכב חכם, בלי לשלם סתם
                    </Text>
                    <Text variant="h2"
                        style={styles.headerH2}>
                        קונים ביטוח רכב אונליין ומקבלים הנחות מותאמות אישית
                    </Text>
                    <Button onLayout={onProcessButtonLayout} mode="contained"
                        onPress={onProccess}
                        rootStyle={styles.proccessButtonRootStyle}
                        style={styles.proccessButtonStyle}
                        contentStyle={styles.proccessButtonContent}
                        labelStyle={[styles.proccessButtonLabel, { fontSize: getButtonProcessFontSize() }]}>
                        3 דקות – ויש לך הצעה!
                    </Button>
                    <Animated.View style={[
                        styles.carContainer,
                        { bottom: 185 + Constants.statusBarHeight },
                        carMovement]}>
                        <Image style={styles.car}
                            source={require('@assets/images/small-car.png')} />
                    </Animated.View>
                </View>
                <View style={styles.content}>
                    <Text variant="h3" style={styles.contentSmartTitle}>
                        אנחנו <Text variant="h2" style={styles.contentSmartSubtitle}>הפניקס Smart</Text>
                    </Text>
                    <Text variant="h3"
                        style={styles.contentSubtitle}>
                        למה לבחור בביטוח מותאם אישית של הפניקס smart?
                    </Text>
                    <View style={styles.infoContainer}>
                        {getInfoBlock(require('@assets/images/flag-icon.png'), `מגוון שירותים\nעצמאיים באתר\nובוואטסאפ`)}
                        {getInfoBlock(require('@assets/images/car-road.png'), `מסלולי ביטוח\nמותאמים אישית לך\nולנהיגה שלך`)}
                        {getInfoBlock(require('@assets/images/phone-road.png'), `ביטול\nהשתתפות\nעצמית`)}
                        {getInfoBlock(require('@assets/images/money-back.png'), `החזר תביעה\nתוך 24 שעות`, 'contain', styles.infoBlockTextBigMargin)}
                    </View>
                    <Text variant="h2"
                        style={styles.coveragesInfoTitle}>
                        חברת ביטוח חכמה{"\n"}
                        עם שירותים דיגיטליים{"\n"}
                        מתקדמים
                    </Text>
                    <View style={styles.coveragesInfoContainer}>
                        {getCoveragesInfo('כיסוי זמני לנהג נוסף')}
                        {getCoveragesInfo('חידוש ביטוח בקליק')}
                        {getCoveragesInfo('דיווח אונליין על תאונה')}
                        {getCoveragesInfo('איתור נותני שירות בכל רגע ומכל מקום')}
                    </View>
                    <View style={styles.questionsContainer}>
                        <Text variant="h2"
                            style={styles.questionsTitle}>
                            שאלות נפוצות
                        </Text>
                        <Text variant="h3"
                            style={styles.questionsSubtitle}>
                            יש לך שאלה? אנחנו כאן כדי לעזור
                        </Text>
                        <View style={styles.questionsListContainer}>
                            {getQuestion('1', 'איך אפשר להתאים ביטוח רכב בהפניקס smart?',
                                'ביטוח הרכב של הפניקס smart מותאם בדיוק לצרכים שלכם, למאפייני הנהיגה והרכב שלכם. הפניקס smart מתגמלת נהגים זהירים ומציעה מגוון הרחבות וכיסויים בהתאמה אישית. בהפניקס smart יצרנו מסלולים המתאימים לנהגים צעירים, נהגים הנוסעים מעט (ביטוח רכב מקיף לפי ק"מ) וכן לנהגים הרוכשים ביטוח צד ג\'.')}
                            {getQuestion('2', 'מה כוללת הפוליסה של הפניקס smart?',
                                'הפניקס smart מציעה את ביטוחי הרכב הבאים: ביטוח חובה, ביטוח צד ג\' וביטוח מקיף לרכב. הביטוחים כוללים, בין היתר, כיסוי להוצאות רפואיות במקרה של תאונה , נזקי מזג אוויר, שריפה, רעידת אדמה ועוד, וכן מפני גניבה או טוטאל לוס של הרכב.')}
                            {getQuestion('3', 'מה משפיע על מחיר ביטוח מקיף לרכב?',
                                'מחיר ביטוח מקיף לרכב בהפניקס smart נקבע ע"פ מאפייני הנהג, מאפייני הנהיגה שלו ומאפייני הרכב. המחיר תלוי רבות בגיל ובוותק של הנהג, בשווי הרכב וברמת הבטיחות שלו.')}
                            {getQuestion('4', 'מה גובה ההשתתפות עצמית במקרה תאונה?',
                                'ההשתתפות עצמית הבסיסית בביטוח המקיף ב smart היא 1995 ₪ , בטיפול במוסך הסדר ניתנת הנחה על המחיר הבסיסי. במקרים מסוימים מחיר ההשתתפות העצמית עשוי להיות גבוה יותר. באפשרותך לרכוש כיסוי "ביטול השתתפות" אשר חוסך את תשלום ההשתתפות העצמית במידה והעלות שלה היא במחיר הבסיס ובעת תיקון במוסך הסדר. כדאי לבדוק, מעת לעת כיסוי זה ניתן ללא תשלום למצטרפים חדשים וגם למחדשים.')}
                        </View>
                        <View style={styles.contactsContainer}>
                            <Text style={styles.contactsHeader} variant="h3">
                                נשמח לדבר בכל דרך שנוחה לך:
                            </Text>
                            <Button style={styles.contactsButton} mode="outlined"
                                onPress={onContactUs} primary>
                                <Text style={styles.contactsButtonText} variant="h6">
                                    צור איתנו קשר
                                </Text>
                            </Button>
                            <View style={styles.contactsInfoContainer}>
                                <Text style={styles.contactsInfoHeader} variant="h3">
                                    כל הדרכים לדבר איתנו:
                                </Text>
                                <IconButton buttonProps={
                                    {
                                        onPress: onPhoneClick,
                                        primary: true,
                                        roundness: 0,
                                    }}
                                    icon={require('@assets/images/phone-icon.png')}
                                    iconStyle={styles.contactsInfoLinkIcon}>
                                    <View style={styles.contactsInfoLinkText}>
                                        <Text style={[styles.contactsInfoLinkTextStyle, rtlStyle]}
                                            variant="h6">בטלפון {appStore.contactPhone}</Text>
                                    </View>
                                </IconButton>
                                <IconButton buttonProps={
                                    {
                                        onPress: onEmailClick,
                                        primary: true,
                                        roundness: 0,
                                    }}
                                    rootStyle={styles.contactsInfoLink}
                                    icon={require('@assets/images/mail-icon.png')}
                                    iconStyle={styles.contactsInfoLinkIcon}>
                                    <View style={styles.contactsInfoLinkText}>
                                        <Text style={[styles.contactsInfoLinkTextStyle, rtlStyle]}
                                            variant="h6">במייל {appStore.contactEmail}</Text>
                                    </View>
                                </IconButton>
                                <IconButton buttonProps={
                                    {
                                        onPress: () => { openLink('https://api.whatsapp.com/send?phone=972548765432') },
                                        primary: true,
                                        roundness: 0,
                                    }}
                                    rootStyle={styles.contactsInfoLink}
                                    icon={require('@assets/images/whatsapp-icon.png')}
                                    iconStyle={styles.contactsInfoLinkIcon}>
                                    <View style={styles.contactsInfoLinkText}>
                                        <Text style={[styles.contactsInfoLinkTextStyle, rtlStyle]}
                                            variant="h6">ב-WhatsApp 0548765432</Text>
                                    </View>
                                </IconButton>
                                <View style={styles.facebookYoutubeContainer}>
                                    <IconButton buttonProps={
                                        {
                                            onPress: () => { openLink('https://www.facebook.com/fnx.co.il/') },
                                            style: styles.facebookYoutubeButton,
                                            primary: true,
                                            roundness: 0,
                                        }}
                                        rootStyle={styles.facebookButtonStyle}
                                        icon={require('@assets/images/facebook-icon.png')}
                                        iconStyle={styles.facebookYoutubeIcon} />
                                    <IconButton buttonProps={
                                        {
                                            onPress: () => { openLink('https://www.youtube.com/user/FNXinsurance') },
                                            style: styles.facebookYoutubeButton,
                                            primary: true,
                                            roundness: 0,
                                        }}
                                        icon={require('@assets/images/youtube-icon.png')}
                                        iconStyle={styles.facebookYoutubeIcon} />
                                </View>
                            </View>
                        </View>
                    </View>
                </View>
                <View style={styles.footer}>
                    <View style={styles.footerAddress}>
                        <Image style={styles.footerLogo} source={require('@assets/images/phoenix_smart_white_icon.png')} />
                        <Text style={styles.footerAddressText} variant="h6">
                            הפניקס חברה לביטוח בע״מ{'\n'}
                            דרך השלום 53 גבעתיים
                        </Text>
                    </View>
                    <Divider style={styles.footerDivider} />
                    <View style={styles.footerInfo}>
                        <Text style={styles.footerInfoText} variant="h6">
                            המחירים וההנחות המפורטים באתר הינם למצטרפים חדשים
                        </Text>
                        <Text style={styles.footerInfoText} variant="h6">
                            קבלת המועמד לביטוח בהתאם לתנאי החיתום הנהוגים בהפניקס
                        </Text>
                        <Text style={styles.footerInfoText} variant="h6">
                            הכיסוי הביטוחי כפוף לתנאי הפוליסה המלאים וסייגיה כפי שמופיעים
                        </Text>
                        <LinkUrl labelStyle={[styles.footerInfoText, styles.underline]}
                            url="https://www.fnx.co.il/">
                            באתר הפניקס
                        </LinkUrl>
                    </View>
                </View>
            </ScrollView>
            {
                contactUsOpened &&
                <ContactUs top={headerHeight()}
                    header="צור קשר"
                    submitButtonText="חזרו אליי"
                    submitButtonLabelStyle={styles.contactUsSubmitButtonLabel} />
            }
        </SafeAreaView>
    </>;
});
export default Landing;
