import Constants from 'expo-constants';
import { action, makeAutoObservable, observable, runInAction } from 'mobx';

class AppStore {
    version = Constants?.manifest?.version;

    @observable loading = true;
    @observable enableCache = true;
    @observable cacheTimeSec = 1;
    @observable cacheType: 'local' | 'session' = 'session';
    @observable contactPhone = '076-599-6187';
    @observable serviceCenterContactPhone = '072-258-4895';
    @observable contactWhatsupUrl = 'https://api.whatsapp.com/send?phone=972548765432';
    @observable contactEmail = 'digital@fnx.co.il';
    @observable layoutHeight = 0;

    private afterMillisecondsTimeout: any;
    constructor() {
        makeAutoObservable(this);
    }

    @action setLoading = (value: boolean, afterMilliseconds: number = 500) => {
        if (value && afterMilliseconds > 0) {
            this.afterMillisecondsTimeout = setTimeout(() => {
                runInAction(() => {
                    this.loading = value;
                });
            }, afterMilliseconds);
        } else {
            if (this.afterMillisecondsTimeout) {
                clearTimeout(this.afterMillisecondsTimeout);
            }
            this.loading = value;
        }
    }

    @action setLayoutHeight = (value: number) => {
        this.layoutHeight = value;
    }
}
export default AppStore;