import { Text } from '@atoms';
import { CustomTheme } from '@theme';
import React from 'react';
import { Linking } from 'react-native';
import { TouchableRipple, useTheme } from 'react-native-paper';
import { IButtonProps } from '../Button/IButtonProps';
import makeStyles from './css';

interface IProps extends IButtonProps {
    url?: string;
}

const LinkUrl = (props: IProps) => {
    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const openLink = async () => {
        if (props.url && await Linking.canOpenURL(props.url)) {
            Linking.openURL(props.url);
        }
    }

    return (
        <TouchableRipple {...props} onPress={props.url ? openLink : props.onPress}
            style={[styles.style, props.style]}>
            <Text style={[styles.text, props.labelStyle]} variant="h5">
                {props.children}
            </Text>
        </TouchableRipple>
    );
};
export default LinkUrl;
