import { Image, Text } from '@atoms';
import { useStore } from '@hooks';
import { DiscountStore } from '@stores';
import { CustomTheme } from '@theme';
import { observer } from 'mobx-react';
import React from 'react';
import { View } from 'react-native';
import { useTheme } from 'react-native-paper';
import makeStyles from './css';

interface IProps {
}

const SideBarMenu = observer((props: IProps) => {
    const discountStore: DiscountStore = useStore('discountStore');

    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    return (
        <View style={styles.root}>
            <Text style={styles.title} variant="h5">
                כבר קיבלת {discountStore.discounts.length} הנחות אישיות:
            </Text>
            <View style={styles.iconsContainer}>
                {discountStore.discounts.map((d, index) => (
                    <View key={d.title} style={[styles.iconContainer, { right: index * 32 }]}>
                        <View style={styles.iconInnerContainer}>
                            <Image style={styles.icon} source={d.icon} />
                        </View>
                    </View>
                ))}
            </View>
        </View>
    );
});
export default SideBarMenu;
