import { AppStore } from '@stores';
import { cLocalStorage, cSessionStorage } from './LocalStorage';

export default class CacheService {
    private appStore: AppStore;
    private cacheStorage = cLocalStorage;

    constructor(appStore: AppStore) {
        this.appStore = appStore;
        if (this.appStore.cacheType === 'session') {
            this.cacheStorage = cSessionStorage;
        }
        this.get('appVersion', new Date())
            .then(r => {
                if (appStore.version !== r) {
                    this.clear();
                    this.add('appVersion', appStore.version);
                }
            });
    }

    public async add(key: string, entry: any) {
        let result
        if (typeof entry !== 'string') {
            entry = JSON.stringify({ date: new Date(), entry })
        }
        result = await this.cacheStorage.setItem(key, entry)
        return result
    }

    public async get<T>(key: string, validUntil: Date | null = null): Promise<T | null> {
        let result: string | null = null
        if (!this.appStore.enableCache) {
            return result as any;
        }
        result = await this.cacheStorage.getItem(key);
        if (result) {
            console.log(`Key: ${key} loaded from cache`)
        }
        if (!result || !result.length) {
            return result as any;
        }
        if (result.indexOf('{') !== 0) {
            return result as any;
        }
        try {
            const parsedResult = JSON.parse(result)
            if (validUntil) {
                if (validUntil < new Date(parsedResult.date)) {
                    return parsedResult.entry;
                } else {
                    console.log(`Key: ${key} date expired. Valid until: ${validUntil}. ExpiredAt: ${parsedResult.date}`)
                    return null;
                }
            } else {
                return parsedResult.entry;
            }
        } catch {
            return result as any;
        }
    }

    public async clear() {
        await this.cacheStorage.clear();
    }
}
