import { Button, IconButton, Text } from '@atoms';
import { useStore } from '@hooks';
import { ContactUsForm } from '@organisms';
import { AppStore } from '@stores';
import { CustomTheme } from '@theme';
import Constants from 'expo-constants';
import { observer } from 'mobx-react';
import React, { useRef } from 'react';
import { Dimensions, Linking, ScrollView, StyleProp, TextStyle, View } from 'react-native';
import { useTheme } from 'react-native-paper';
import { IContactUsFormHandle } from '../../organisms/ContactUsForm';
import makeStyles from './css';

interface IProps {
    top: number;
    header: string;
    submitButtonText?: string;
    submitButtonBottom?: boolean;
    submitButtonLabelStyle?: StyleProp<TextStyle>;
}

const ContactUs = observer((props: IProps) => {
    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    const appStore: AppStore = useStore('appStore');
    const deviceHeight = Dimensions.get('window').height;

    const submitButtonText = props.submitButtonText ?? 'המשך';

    const contactUsForm = useRef<IContactUsFormHandle>(null);
    const onSubmit = () => {
        contactUsForm.current?.submitForm();
    };

    const onPhoneClick = () => {
        const phone = `tel: ${appStore.serviceCenterContactPhone}`;
        Linking.openURL(phone);
    }

    const onWhatsUpClick = async () => {
        if (await Linking.canOpenURL(appStore.contactWhatsupUrl)) {
            Linking.openURL(appStore.contactWhatsupUrl);
        }
    }

    const getBottom = () => {
        if (!appStore.layoutHeight) {
            return 0;
        }
        return deviceHeight - appStore.layoutHeight + Constants.statusBarHeight;
    };

    return (
        <View style={[
            styles.root,
            {
                top: props.top,
                bottom: getBottom(),
            },
            props.submitButtonBottom ? { paddingBottom: 0 } : null
        ]}>
            <Text style={styles.header} variant="h5">
                {props.header}
            </Text>
            <ScrollView scrollEventThrottle={2}
                style={styles.scrollContainer}>
                <Text style={styles.title} variant="h5">
                    פשוט ממלאים את הפרטים ונציג ייצור איתך קשר בהקדם
                </Text>
                <View style={styles.subjectContainer}>
                    <Text style={styles.subjectTitle}>נושא הפניה</Text>
                </View>
                <ContactUsForm ref={contactUsForm} />
                {
                    !props.submitButtonBottom &&
                    <Button onPress={onSubmit} mode="contained"
                        style={styles.submitButton}
                        labelStyle={props.submitButtonLabelStyle}>
                        {submitButtonText}
                    </Button>
                }
                <View style={styles.contactsContainer}>
                    <Text style={styles.contactsHeader} variant="h3">
                        מוקד השירות של Smart
                    </Text>
                    <IconButton iconStyle={styles.contactsButtonIcon}
                        icon={require('@assets/images/phone.png')}
                        rootStyle={styles.contactsButtonContainer}
                        buttonProps={{
                            primary: true,
                            onPress: onPhoneClick,
                            style: styles.contactsButtonStyle,
                        }}
                        childrenContainerStyle={styles.contactsButtonChildrenContainerStyle}>
                        <Text style={styles.contactsButtonText} variant="h5">{appStore.serviceCenterContactPhone}</Text>
                    </IconButton>
                    <Text style={[styles.contactsHeader, styles.contactsWhatsUpHeader]} variant="h3">
                        שיחה באמצעות WhatsApp
                    </Text>
                    <IconButton iconStyle={styles.contactsButtonIcon}
                        icon={require('@assets/images/whatsup.png')}
                        rootStyle={styles.contactsButtonContainer}
                        buttonProps={{
                            primary: true,
                            onPress: onWhatsUpClick,
                            style: styles.contactsButtonStyle,
                        }}
                        childrenContainerStyle={styles.contactsButtonChildrenContainerStyle}>
                        <Text style={styles.contactsButtonText} variant="h5">שלח הודעה</Text>
                    </IconButton>
                    <View style={styles.workHoursContainer}>
                        <Text style={styles.workHoursBlock}>
                            מכירות:{'\n'}
                            א׳-ה׳ 8:00-20:00{'\n'}
                            ו׳ 8:00-13:00{'\n'}
                        </Text>
                        <View style={styles.separator} />
                        <Text style={styles.workHoursBlock}>
                            שירות לקוחות:{'\n'}
                            א׳-ה׳ 8:00-17:00
                        </Text>
                    </View>
                </View>
            </ScrollView>
            {
                props.submitButtonBottom &&
                <Button onPress={onSubmit} mode="contained"
                    style={[styles.submitButton, styles.submitButtonBottom]}
                    textStyle={props.submitButtonLabelStyle}>
                    {submitButtonText}
                </Button>
            }
        </View>
    );
});
export default ContactUs;
