import { Checkbox, LinkUrl, Text, ToggleButton } from '@atoms';
import { useProposal, useStore } from '@hooks';
import { IOption } from '@models/IKeyValue';
import { IProposalEntry } from '@models/IProposalEntry';
import { UlList } from '@molecules';
import { Footer } from '@organisms';
import { baseHref } from '@routes';
import { AppStore, ProposalStore } from '@stores';
import { CustomTheme } from '@theme';
import { useFormik } from 'formik';
import { observer } from 'mobx-react';
import React, { useEffect, useState } from 'react';
import { View } from 'react-native';
import { useTheme } from 'react-native-paper';
import { useNavigate } from 'react-router';
import * as Yup from 'yup';
import makeStyles from './css';

const InsuranceType = observer(() => {
    const appStore: AppStore = useStore('appStore');
    const proposalStore: ProposalStore = useStore('proposalStore');

    const theme = useTheme();
    const styles = makeStyles(theme as CustomTheme);

    useEffect(() => {
        setTimeout(() => {
            appStore.setLoading(false);
        }, 300);//3000); //
    }, []);

    const proposal = useProposal(proposalStore);

    const insuranceTypes: IOption[] = [
        { key: 1, value: 'ביטוח מקיף + חובה' },
        { key: 3, value: `ביטוח צד ג' + חובה` },
    ];
    const insuranceTypeError = 'אנחנו חייבים את המידע הזה כדי להמשיך להצעה';

    const validationSchema = Yup.object().shape({
        insuranceType: Yup.number()
            .required(insuranceTypeError)
            .oneOf(insuranceTypes.map(opt => parseInt(opt.key.toString())), insuranceTypeError),
        termsApproval: Yup.bool()
            .oneOf([true], 'נוכל לקבל את הפרטים ולחשב את המחיר רק אחרי האישור שלך'),
    });

    const navigate = useNavigate();
    const onSubmit = async (values: IProposalEntry) => {
        if (formik.isValid) {
            appStore.setLoading(true);
            await proposalStore.setProposal(values);
            navigate(`/${baseHref}/stageOne/carOwnerFirstName`);
        }
    };

    const formik = useFormik({
        initialValues: proposal,
        validationSchema,
        onSubmit: onSubmit,
        enableReinitialize: true,
    });

    const [formSubmited, setFormSubmited] = useState(false);
    const onSubmitButton = () => {
        setFormSubmited(true);
        formik.submitForm();
    }

    const getFieldTouched = (fieldName: string) => {
        return formSubmited || (formik.touched as any)[fieldName];
    }

    return (
        <View style={styles.root}>
            <Text style={styles.pageTitle} variant="h1">
                ביטוח רכב חכם,{'\n'}
                בלי לשלם סתם
            </Text>
            <Text style={styles.subtitle} variant="h5">
                נשאל אותך כמה שאלות ונציג לך את הצעות הביטוח{'\n'}
                שהכי מתאימות לך - וכמובן, הכי משתלמות :)
            </Text>
            <Text style={styles.buttonsTitle} variant="h3">
                איזה סוג ביטוח מתאים לך?
            </Text>
            <View style={styles.togglesContainer} accessibilityRole="radiogroup">
                {insuranceTypes.map(opt => (
                    <ToggleButton key={opt.key}
                        fieldName="insuranceType"
                        value={opt.key}
                        setFieldTouched={formik.setFieldTouched}
                        setFieldValue={formik.setFieldValue}
                        checked={formik.values.insuranceType === opt.key}
                        error={getFieldTouched('insuranceType') && !!formik.errors.insuranceType}
                        buttonProps={{
                            primary: true,
                            rootStyle: styles.insuranceTypeButton,
                            style: styles.insuranceTypeButtonStyle,
                            contentStyle: styles.insuranceTypeButtonContent,
                            labelStyle: styles.insuranceTypeButtonLabel,
                            multiline: true,
                            // accessibilityRole: 'radio',
                        }}>
                        {opt.value}
                    </ToggleButton>
                ))}
            </View>
            <Text style={styles.error} variant="error">
                {getFieldTouched('insuranceType') ? (formik.errors.insuranceType ?? ' ') : ' '}
            </Text>
            <Checkbox status={formik.values.termsApproval ? 'checked' : 'unchecked'}
                error={getFieldTouched('termsApproval') && !!formik.errors.termsApproval}
                fieldName="termsApproval"
                setFieldTouched={formik.setFieldTouched}
                setFieldValue={formik.setFieldValue}>
                <Text style={styles.checkboxText}>
                    קראתי ואישרתי את{' '}
                </Text>
                <LinkUrl labelStyle={[[styles.checkboxText, styles.underline]]}
                    accessibilityLabel="תנאי השימוש קישור לאתר חיצוני"
                    url="https://www.fnx.co.il/Pages/terms-of-use-car-online.aspx">
                    תנאי השימוש
                </LinkUrl>
                <Text style={styles.checkboxText}> ואת </Text>
                <LinkUrl labelStyle={[styles.checkboxText, styles.underline]}
                    accessibilityLabel="מדיניות שמירת הפרטיות קישור לאתר חיצוני"
                    url="https://www.fnx.co.il/security-and-terms-of-use/privacy-policy.aspx">
                    מדיניות שמירת הפרטיות
                </LinkUrl>
            </Checkbox>
            <Text style={styles.error} variant="error">
                {getFieldTouched('termsApproval') ? (formik.errors.termsApproval ?? ' ') : ' '}
            </Text>
            <Footer onSubmitButton={onSubmitButton}>
                <View style={styles.footerInfoContainer}>
                    <UlList options={[
                        <Text style={styles.footerInfoText}>
                            הצעת המחיר מיועדת למצטרפים חדשים
                        </Text>,
                        <Text style={styles.footerInfoText}>
                            מעוניין לרכוש ביטוח חובה בלבד? פנה למוקד בטלפון{' '}
                            <LinkUrl labelStyle={[styles.phoneNumber, styles.underline]}
                                accessibilityLabel="מדיניות שמירת הפרטיות קישור לאתר חיצוני"
                                url={`tel:${appStore.serviceCenterContactPhone}`}>
                                {appStore.serviceCenterContactPhone}
                            </LinkUrl>
                        </Text>
                    ]} />
                </View>
            </Footer>
        </View>
    );
});
export default InsuranceType;
