import { Text } from '@atoms';
import { CustomTheme } from '@theme';
import React from 'react';
import { StyleProp, TextStyle, View } from 'react-native';
import { RadioButton as PaperRadioButton, TouchableRipple, useTheme } from 'react-native-paper';
import makeStyles from './css';

interface IProps {
    label?: string;
    value: string;
    status?: 'checked' | 'unchecked';
    onValueChange?: (value: string) => void;
    children?: React.ReactNode;
    title?: string;
    primary?: boolean;
    collapsedDescription?: string;
    descriptionStyle?: StyleProp<TextStyle>;
    checkedIcon?: React.ReactNode;
    uncheckedIcon?: React.ReactNode;
}

const RadioButton = (props: IProps) => {
    const theme = useTheme() as CustomTheme;
    const styles = makeStyles(theme);

    const label = props.label ?? '';
    const checked = props.status === 'checked';

    const rtlStyle: StyleProp<TextStyle> = { direction: 'rtl' };

    const onToggleRadio = () => {
        if (props.onValueChange) {
            props.onValueChange(props.value);
        }
    }

    const checkedColor = theme.colors[props.primary ? 'primary' : 'accent'];
    const uncheckedColor = theme.colors[props.primary ? 'primary' : 'accent'];

    if (props.checkedIcon && props.uncheckedIcon) {
        return (
            <View style={styles.root}>
                <TouchableRipple onPress={onToggleRadio}
                    accessibilityRole="radio"
                    rippleColor={theme.colors.lightBlue}
                    style={styles.buttonRoot}>
                    <View style={[styles.buttonWithIcon, rtlStyle]}>
                        <View style={styles.buttonIcon}>
                            {checked && props.checkedIcon}
                            {!checked && props.uncheckedIcon}
                        </View>
                        <View style={styles.buttonTextContainer}>
                            <Text style={styles.buttonText} variant="h6">
                                {props.title}
                            </Text>
                            {
                                checked && !!props.collapsedDescription &&
                                <Text style={props.descriptionStyle} variant="h6">
                                    {props.collapsedDescription}
                                </Text>
                            }
                        </View>
                    </View>
                </TouchableRipple>
            </View>
        )
    }
    return (
        <PaperRadioButton.Item {...props}
            label={label}
            color={checkedColor} uncheckedColor={uncheckedColor} />
    );
};
export default RadioButton;
